body {
    background-color: #000000;
    font-family: 'Lato', sans-serif;
}

::-webkit-scrollbar {
    display: none;
}

.my-container {
    text-align: left;
    padding-right: 30vw;
    color: white;
    height: 90vh;
    overflow-y: scroll;
}

.home-name {
    font-size: 4em;
    padding-top: 10vh;
}

.home-text {
    font-size: 3em;
    font-weight: normal;
    padding: 50px 0;
}

.home-text a {
    color: #888888;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.home-text a:hover {
    text-decoration: none;
    color: #DDDDDD;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

/* If the screen size is 600px wide or less, set the font-size of <div> to 30px */
@media screen and (max-width: 600px) {
    .home-name {
      font-size: 3em;
    }

    .home-text{
        font-size: 1.5em;
        padding: 30px 0;
    }

    .my-container {
        /* padding-left: 0.5rem 1.5rem!important;
        padding-right: 20vw; */
        padding: 0.5rem 20vw 0.5rem 1.5rem;
    }
}
.scroll-container {
    /* height: 90vh; */
    /* padding: 0 20vw; */
    padding-top: 20vh;
    /* overflow: scroll; */
}

.project-header {
    padding: 30px 0;
    color: #DDDDDD;
}

.scroll-indicator-container {
    text-align: center;
    margin: 5vh 0;
}

.scroll-indicator-container img {
    width: 50px;
    height: 50px;
}

.parent {
    position: relative;
    display: flex;
}

.col {
    flex-direction: column;
}

.section-container {
    height: 40vh;
    width: 100%;
    box-sizing: border-box;
    overflow-y: scroll;
    -ms-scroll-snap-type: y mandatory;
        scroll-snap-type: y mandatory;
    position: absolute;
    left: 0;
    top: 0;
    cursor: pointer;
}

.section-waypoint {
    padding: 15vh 5vw;
}

section {
    /* height: 40vh; */
    scroll-snap-align: center;
    color: black;
}

section h3 {
    color: white;
    font-size: 2em;
    padding-top: 0;
    text-shadow: 1px 1px 1px black;
    font-family: 'Volkhov', serif;
    /* padding: 15vh 0; */
}

#mobile-surface {
    display: none;
}

.location-desc, .location-name {
    color: white;
    font-family: 'Volkhov', serif;
}

@media screen and (max-width: 600px) {
    .parent {
        display: block;
    }

    .col {
        margin: 10vh 0;
    }

    .scroll-indicator-container {
        display: none;
    }

    #desktop-surface {
        display: none;
    }

    #mobile-surface {
        display: block;
    }

    .scroll-container {
        padding-top: 10vh;
    }

    .col-mobile {
        height: 25vh;
    }

    .section-container {
        height: 200px;
    }
}
